import { Component } from '@angular/core';
import { NavController, NavParams, IonicPage } from 'ionic-angular';
import { Values } from '../../../providers/service/values';
import { Service } from '../../../providers/service/service';
import { Functions } from '../../../providers/service/functions';


@IonicPage()
@Component({
  templateUrl: 'privacy-policy.html',
})
export class PrivacyPolicy {

  href: any;
  title: any;
  policy: any;

  
  constructor(public nav: NavController, public service: Service, params: NavParams, public functions: Functions, public values: Values) {

  	this.href = params.data.agree_link;
    this.title = params.data.title;

    this.service.loadPolicy(this.href)
    .then((results) => this.handlePolicy(results));
  }

    
  handlePolicy(results){
    this.policy = results;
  }  
 
}





